<template>
  <div class="MoneyHud noselect">
    <b-container fluid>
      <b-row class="moneyhud mt-5">
        <b-col
          class="d-flex justify-content-end moneybtn text-right"
          offset-md="auto"
        >
          <b-button @click="moneyButton">
            <i class="fas fa-dollar-sign fa-xs" />
          </b-button>
          <transition name="expand">
            <div
              v-show="showMoney"
              class="money border border-secondary"
              style="width: 8.999%"
            >
              <div class="p1 inline-block h-50">
                <i class="fas fa-university" />
                {{ MoneyBank }}
              </div>
              <div class="p1 inline-block h-50">
                <i class="fas fa-wallet" />
                {{ MoneyCash }}
              </div>
            </div>
          </transition>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import {
  BContainer,
  BRow,
  BCol,
  // BButton,
} from "bootstrap-vue";
export default {
  data() {
    return {
      showMoney: false,
    };
  },
  components: {
    BContainer,
    BRow,
    BCol,
    // BButton,
  },
  props: {
    MoneyBank: Number,
    MoneyCash: Number,
  },
  methods: {
    moneyButton: function(event) {
      this.showMoney = !this.showMoney;
      event.target.blur();
    },
  },
  mounted() {},
};
</script>

<style scoped lang="scss">
$base-color: #000000;
$secondary-color: #ffa600;
$secondary-darker-color: #e77000;
$theme-colors: (
  primary: $base-color,
  secondary: $secondary-color,
);
/* Enter and leave animations can use different */
/* durations and timing functions.              */
.slide-fade-enter-active {
  transition: 1s;
}
.slide-fade-leave-active {
  transition: 1s;
}
.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(50px);
  opacity: 0;
}
.expand-leave-active,
.expand-enter-active {
  transition: 1s;
}
.expand-enter {
  margin-right: -8%;
  opacity: 0;
}
.expand-enter-to {
  margin-right: 0;
  opacity: 1;
}
.expand-leave-to {
  margin-right: -9%;
  opacity: 0;
}
.p1 {
  font-size: 12px;
  font-weight: bold;
  color: $secondary-color;
  padding-left: 5px;
}
.btn-secondary {
  background-color: rgba($base-color,0.50);
  border-color: $secondary-darker-color;
  margin-right: 2px;
  max-height: 100%;
  min-height: 100%;
}
.btn:hover {
  background-color: $secondary-color;
}
.btn:focus,
.btn:active {
  outline: none !important;
  box-shadow: none;
}
.money {
  background-color: rgba($base-color,0.50);
  border-color: $secondary-darker-color !important;
}
.fa-university {
  color: rgb(255, 217, 0);
  padding-right: 5px;
}
.fa-dollar-sign {
  color: $secondary-darker-color;
}
.fa-wallet {
  color: green;
  padding-right: 5px;
}
.row > * {
  padding-right: 0 !important;
}
