<template>
  <div class="hud noselect">
    <MoneyHud :MoneyBank="this.MoneyBank" :MoneyCash="this.MoneyCash"/>
  </div>
</template>

<script>
import MoneyHud from './../components/MoneyHud.vue'
export default {
  components:{
    MoneyHud
  },
  data(){
    return{
      MoneyBank: 88888,
      MoneyCash: 9878451
    }
  },
  mounted(){
    window.mp.events.add(
      "HUD:DisplayMoney",
      (bank, cash) => {
        this.MoneyBank = bank;
        this.MoneyCash = cash;
      }
    );

    window.mp.events.add(
      "HUD:UpdateMoney",
      (bank, cash) => {
        this.MoneyBank = bank;
        this.MoneyCash = cash;
      }
    );
  }
}
</script>

