<template>
  <div class="Authentication vh-100 d-flex align-items-center">
    <b-row class="authwindow border border-secondary">
      <b-col sm="6" class="auth">
        <b-img
          thumbnail
          v-tilt
          center
          src="./assets/Authentication/avatar.png"
          class="logo" @click="poweredByhover=!poweredByhover"
        ></b-img>
      </b-col>
      <b-col sm="6" class="auth">
        <b-form @submit="onRegister" @reset="onReset" v-if="showRegistration">
          <h2>{{ username }}</h2>
          <b-form-group>
            <b-form-input 
              class="border border-secondary mt-1"
              id="input-1"
              v-model="form.email"
              type="email"
              placeholder="Enter email"
              required
            ></b-form-input>
          </b-form-group>

          <b-form-group id="input-group-2">
            <b-form-input
              class="mt-2 border border-secondary"
              type="password"
              id="input-2"
              v-model="form.password"
              placeholder="Password"
              required
            ></b-form-input>
          </b-form-group>

          <b-row class="mt-2">
            <b-button block type="submit" variant="primary">Register</b-button>
          </b-row>

          <b-row class="mt-2">
            <b-button block type="reset" variant="danger">Reset</b-button>
          </b-row>
        </b-form>
        <b-form
          @submit="onLogin"
          @reset="onResetPassword"
          v-else-if="showLogin"
        >
          <h2 class="mb-3">Project Y</h2>
          <div class="border-top border-secondary pt-4"> <p class="p1">{{ username }}</p></div>
          <b-form-group id="input-group-2">
            <b-form-input
              class="mt-2 border border-secondary"
              type="password"
              id="input-2"
              v-model="form.password"
              placeholder="Password"
              required
            ></b-form-input>
          </b-form-group>
          
          <b-popover :show.sync="badPassword" target="badPasswordPop" title="Popover">
            Incorrect password, check your spelling
          </b-popover>
          <b-row class="mt-4">
            <b-button id="badPasswordPop" block type="submit" variant="primary"
              >Login</b-button
            >
          </b-row>

          <b-row class="mt-4">
            <b-button block type="reset" variant="danger"
              >Reset password</b-button
            >
          </b-row>
        </b-form>
      </b-col>
      <vue-slide-toggle :open="poweredByhover" tag="div" :duration="1000" class="row m-0">
          <b-col sm="6" class="border-top border-secondary pt-4"> <p>Powered by</p> </b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom :popover-style="{background: '#ffa600'}" title="Vue" square class="hvr-grow" src="./assets/Authentication/icon/vue.png"></b-avatar></b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom title="CSharp" square class="hvr-grow" src="./assets/Authentication/icon/csharp.png"></b-avatar></b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom title="MongoDB" square class="hvr-grow" src="./assets/Authentication/icon/mongodb.png"></b-avatar></b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom title="RAGE:MP" square class="hvr-grow" src="./assets/Authentication/icon/mp.png"></b-avatar></b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom title="CSS3" square class="hvr-grow" src="./assets/Authentication/icon/css3.png"></b-avatar></b-col>
          <b-col sm="1" class="border-top border-secondary"><b-avatar v-b-tooltip.hover.bottom title="HTML5" square class="hvr-grow" src="./assets/Authentication/icon/html5.png"></b-avatar></b-col>
      </vue-slide-toggle>
    </b-row>
  </div>
</template>

<script>
export default {
  name: "Auth",
  data() {
    return {
      username: "Dziugauskis",
      form: {
        email: "",
        password: "",
        rememberMe: false,
      },
      showRegistration: false,
      showLogin: true,
      badPassword: false,
      poweredByhover: false,
    };
  },
  methods: {
    onRegister(event) {
      event.preventDefault();
      window.mp.trigger(
        "Authentication:Register",
        this.form.password,
        this.form.email
      );
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.form.email = "";
      this.form.password = "";
      // Trick to reset/clear native browser form validation state
      this.showRegistration = false;
      this.$nextTick(() => {
        this.showRegistration = true;
      });
    },
    onLogin(event) {
      event.preventDefault();
      window.mp.trigger("Authentication:TryLogin", this.form.password);
    },
    onResetPassword(event) {
      event.preventDefault();
      // Reset our form values
      this.form.password = "";
      // Trick to reset/clear native browser form validation state
      this.showLogin = false;
      this.$nextTick(() => {
        this.showLogin = true;
      });
    },
  },
  mounted() {
    window.mp.events.add(
      "Authentication:InitializeLoginScreen",
      (socialName) => {
        this.username = socialName;
        this.showLogin = true;
      }
    );
    window.mp.events.add(
      "Authentication:InitializeRegisterScreen",
      (socialName) => {
        this.username = socialName;
        this.showRegistration = true;
      }
    );
    window.mp.events.add("Authentication:BadPassword", () => {
      this.badPassword = true;
      setTimeout(() => {
        this.badPassword = false;
      }, 1000);
    });

    window.mp.events.add("Authentication:InitHud", () => {
      this.$router.push("Hud");
    });

    window.mp.events.add("Authentication:InitCreator", () => {
      this.$router.push("CharacterCreator");
    });
  },
};
</script>

